import React from 'react';
import SwiperCore, {A11y, Navigation, Pagination, Scrollbar} from 'swiper';

import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/scrollbar/scrollbar.scss';
import "../../assets/css/portfolio.css"

import Paiwi_1 from "../../assets/img/portfolio/paiwi/paiwi-1.png"
import Paiwi_2 from "../../assets/img/portfolio/paiwi/paiwi-2.png"
import CHITS_1 from "../../assets/img/portfolio/chits/chits-1.png"
import CHITS_2 from "../../assets/img/portfolio/chits/chits-3.png"
import Inventory_1 from "../../assets/img/portfolio/inventory/inventory_1.png"
import Inventory_2 from "../../assets/img/portfolio/inventory/inventory_2.png"
import Inventory_3 from "../../assets/img/portfolio/inventory/inventory_3.png"

import {Col, Container, Image, Row} from "react-bootstrap";
import {Swiper, SwiperSlide} from 'swiper/react';

SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

const Project = () => {
    return (
        <Container id="project">
            <Row className="align-items-center full-height p-5">
                <Col className="col-auto col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <p>
                        An online android application which tracks real-time investment assets and status.
                    </p>
                    <p>
                        Skills: Java, Android, Amazon S3, Firebase, Rest API
                    </p>
                </Col>
                <Col className="col-auto col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <Swiper
                        autoplay={true}
                        spaceBetween={5}
                        slidesPerView={1}
                        pagination={{clickable: true}}
                    >
                        <SwiperSlide><Image alt="test" className="img-fluid image-content" rounded={true}
                                            src={Paiwi_1}/></SwiperSlide>
                        <SwiperSlide><Image alt="test" className="img-fluid image-content" rounded={true}
                                            src={Paiwi_2}/></SwiperSlide>
                    </Swiper>
                </Col>
            </Row>
            <Row className="align-items-center full-height p-5">
                <Col className="col-auto col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <p>
                        CHITS is an electronic medical record system that reduces patient waiting time and improves
                        monitoring of patient care through efficient data encoding and records retrieval
                    </p>
                    <p>
                        Skills: Java, Spring MVC, Hibernate,MySQL, Ant
                    </p>
                </Col>
                <Col className="col-auto col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <Swiper
                        autoplay={true}
                        spaceBetween={5}
                        slidesPerView={1}
                        pagination={{clickable: true}}
                    >
                        <SwiperSlide><Image className="img-fluid image-content" rounded={true}
                                            src={CHITS_1}/></SwiperSlide>
                        <SwiperSlide><Image className="img-fluid image-content" rounded={true}
                                            src={CHITS_2}/></SwiperSlide>
                    </Swiper>
                </Col>
            </Row>
            <Row className="align-items-center full-height p-5">
                <Col className="col-auto col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <p>

                    </p>
                    <p>
                        Skills: Java, JavaFX, Spring MVC, Hibernate, MySQL, Maven
                    </p>
                </Col>
                <Col className="col-auto col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <Swiper
                        autoplay={true}
                        spaceBetween={5}
                        slidesPerView={1}
                        pagination={{clickable: true}}
                    >
                        <SwiperSlide><Image className="img-fluid image-content" rounded={true} fluid="true"
                                            src={Inventory_3}/></SwiperSlide>
                        <SwiperSlide><Image className="img-fluid image-content" rounded={true} fluid="true"
                                            src={Inventory_1}/></SwiperSlide>
                        <SwiperSlide><Image className="img-fluid image-content" rounded={true} fluid="true"
                                            src={Inventory_2}/></SwiperSlide>

                    </Swiper>
                </Col>
            </Row>
        </Container>

    )
};
export default Project;